.footer {
  background-color: #262626;
  padding-top: 63px;
  padding-bottom: 92px;

  &__top-part {
    display: flex;
    border-bottom: 1px solid #C4C4C4;
    margin-bottom: 40px;
    padding-bottom: 40px;
  }

  &__main {
    flex-basis: 348px;

  }

  &__logo-container {
    display: flex;
    justify-content: space-between;

  }

  &__select-language {
    width: 164px;
  }

  &__mobile-container {
    margin-top: 55px;
    display: flex;
    justify-content: space-between;

  }

  &__links-container {
    display: flex;
    margin-left: 204px;
  }

  &__link {
    color: #FFFFFF;
    white-space: nowrap;

    &:hover{
      text-decoration: underline;
    }

    &:not(:first-child){
      margin-top: 16px;
    }
  }

  &__links {
    display: flex;
    flex-direction: column;



    &:nth-child(even){
      margin-left: 136px;
    }
  }

  &__bottom-part {
    flex-wrap: wrap;
    display: flex;
  }

  &__copyright {
    font-size: 14px;
    color: #FFFFFF;
  }

  &__info {
    margin-left: 33%;
    display: flex;
  }

  &__info-link {
    color: #FFFFFF;
    white-space: nowrap;

    &:hover{
      text-decoration: underline;
    }

    &:not(:first-child) {
      margin-left: 48px;
    }
  }

  &__social {
    margin-top: 56px;
    margin-left: 50.5%;
  }

  &__social-link {
    &:not(:first-child) {
      margin-left: 24px;
    }
  }
}
