@import "../../styles/extends";

.header {
  position: sticky;
  z-index: 1;
  top: 0;
  padding: 16px 0;
  background-color: white;
  &__inner {
    @extend %flex-row-center
  }

  &__delivery-info {
    @extend %flex-row-center;
    margin-left: 7vw;
  }

  &__search {
    margin-left: auto;
  }

  &__link {
    margin-left: 40px;
    color: #1F1F1F;
    padding: 12px 0;
    white-space: nowrap;

    &:hover {
      text-decoration: underline;
    }
  }
}
